
























































































import { Vue, Component } from 'vue-property-decorator';
import { MyData } from '@/types/views/my.interface';
import BaseImg from '@/components/BaseImg.vue';
import BaseAvatar from '@/components/BaseAvatar.vue';

@Component({
  components: {
    BaseImg,
    BaseAvatar
  }
})
export default class My extends Vue {
  defaultUserInfo!: object; // 未登录，默认用户信息
  userInfo!: object; // 用户信息
  // tabItems!: object[]; // 选项卡
  activeTab!: number; // 当前选中的选项卡
  tabLoading!: boolean; // 选项卡内容加载中

  data(): MyData {
    return {
      defaultUserInfo: {}, // 未登录，默认用户信息
      userInfo: {}, // 用户信息
      // tabItems: [], // 选项卡
      activeTab: 0, // 当前选中的选项卡
      tabLoading: false // 选项卡内容加载中
    };
  }

  // 路由跳转相关
  goToPage(to: any) {
    if (to.name) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.name === to.name) {
        return;
      }
      this.$router.push({
        name: to.name
      });
    } else if (to.path) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.path === to.path) {
        return;
      }
      this.$router.push({
        path: to.path
      });
    }
  }

  // 背景图高度
  get bgHeight() {
    if (this.$vuetify.breakpoint.xs) {
      return 140;
    } else if (this.$vuetify.breakpoint.sm) {
      return 180;
    } else if (this.$vuetify.breakpoint.md) {
      return 240;
    } else if (this.$vuetify.breakpoint.lg) {
      return 360;
    }
    return 400;
  }

  // 头像大小
  get avatarSize() {
    if (this.$vuetify.breakpoint.xs) {
      return 64;
    } else if (this.$vuetify.breakpoint.sm) {
      return 88;
    } else if (this.$vuetify.breakpoint.md) {
      return 104;
    } else if (this.$vuetify.breakpoint.lg) {
      return 112;
    }
    return 120;
  }

  // 首推选项卡高度
  get tabsHeight() {
    if (this.$vuetify.breakpoint.md) {
      return 56;
    } else if (this.$vuetify.breakpoint.lgAndUp) {
      return 64;
    }
    return '';
  }

  // 获取用户信息后台数据
  fetchDefaultUserInfoData() {
    // this.$api.userApi.my.getDefaultUserInfo().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.userInfo = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    // this.defaultUserInfo = this.$api.userApi.my.getDefaultUserInfo().data;
    this.tabLoading = true;
    setTimeout(() => {
      this.tabLoading = false;
      this.defaultUserInfo = this.$api.userApi.my.getDefaultUserInfo().data;
    }, 400);
  }

  // 获取用户信息后台数据
  fetchUserInfoData() {
    // this.$api.userApi.my.getUserInfo().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.userInfo = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    // this.userInfo = this.$api.userApi.my.getUserInfo().data;
    this.tabLoading = true;
    setTimeout(() => {
      this.tabLoading = false;
      this.userInfo = this.$api.userApi.my.getUserInfo().data;
    }, 400);
  }

  // 生命周期钩子函数
  created() {
    // this.fetchDefaultUserInfoData();
    this.defaultUserInfo = this.$api.userApi.my.getDefaultUserInfo().data;
    // this.fetchUserInfoData();
    this.userInfo = this.$api.userApi.my.getUserInfo().data;
  }
}
